"use client"

import { useEffect, useMemo, useState } from "react"
import Link from "next/link"
import { z } from "zod"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod"
import { Plus, BookText, RotateCw, AlertCircle } from "lucide-react"

import api from "@/lib/api"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Separator } from "@/components/ui/separator"
import { Skeleton } from "@/components/ui/skeleton"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form"

type Subject = { id: string; name: string }

const createSchema = z.object({
  name: z
    .string()
    .min(2, "Name must be at least 2 characters")
    .max(64, "Name must be at most 64 characters"),
})

type CreateValues = z.infer<typeof createSchema>

export default function DashboardPage() {
  const [subjects, setSubjects] = useState<Subject[] | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [refreshing, setRefreshing] = useState(false)

  const form = useForm<CreateValues>({
    resolver: zodResolver(createSchema),
    defaultValues: { name: "" },
  })

  const hasSubjects = useMemo(() => (subjects?.length ?? 0) > 0, [subjects])

  async function fetchSubjects() {
    try {
      setError(null)
      setRefreshing(true)
      const res = await api.get<Subject[]>("/subjects")
      setSubjects(res.data)
    } catch (e: any) {
      const message = e?.response?.data?.message || "Failed to load subjects"
      setError(String(message))
    } finally {
      setLoading(false)
      setRefreshing(false)
    }
  }

  useEffect(() => {
    // initial load
    fetchSubjects()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onCreate = async (values: CreateValues) => {
    try {
      setError(null)
      await api.post("/subjects", values)
      form.reset({ name: "" })
      await fetchSubjects()
    } catch (e: any) {
      const message = e?.response?.data?.message || "Failed to create subject"
      setError(String(message))
    }
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-semibold tracking-tight">Your Subjects</h1>
          <p className="text-muted-foreground text-sm">Create and manage the subjects you study.</p>
        </div>
        <Button variant="outline" onClick={fetchSubjects} disabled={refreshing}>
          <RotateCw className={`mr-2 h-4 w-4 ${refreshing ? "animate-spin" : ""}`} /> Refresh
        </Button>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Create a new subject</CardTitle>
          <CardDescription>Add a subject to begin organizing your study materials.</CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...form}>
            <form onSubmit={form.handleSubmit(onCreate)} className="flex flex-col gap-3 sm:flex-row">
              <FormField
                control={form.control}
                name="name"
                render={({ field }) => (
                  <FormItem className="flex-1">
                    <FormLabel className="sr-only">Subject name</FormLabel>
                    <FormControl>
                      <Input placeholder="e.g. Linear Algebra" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <Button type="submit" disabled={form.formState.isSubmitting}>
                <Plus className="mr-2 h-4 w-4" />
                {form.formState.isSubmitting ? "Creating..." : "Create"}
              </Button>
            </form>
          </Form>
        </CardContent>
      </Card>

      {error && (
        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertTitle className="ml-2">Something went wrong</AlertTitle>
          <AlertDescription className="ml-6">{error}</AlertDescription>
        </Alert>
      )}

      {loading ? (
        <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
          {Array.from({ length: 6 }).map((_, i) => (
            <Skeleton key={i} className="h-28 w-full" />
          ))}
        </div>
      ) : hasSubjects ? (
        <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
          {subjects!.map((s) => (
            <Link key={s.id} href={`/subjects/${s.id}`} className="group">
              <Card className="transition-colors hover:border-foreground/30">
                <CardHeader className="flex flex-row items-center gap-3">
                  <div className="rounded-md border bg-background p-2 text-foreground/80 group-hover:text-foreground">
                    <BookText className="h-5 w-5" />
                  </div>
                  <div>
                    <CardTitle className="leading-tight">{s.name}</CardTitle>
                    <CardDescription>Open workspace</CardDescription>
                  </div>
                </CardHeader>
              </Card>
            </Link>
          ))}
        </div>
      ) : (
        <Card className="border-dashed">
          <CardHeader>
            <CardTitle>No subjects yet</CardTitle>
            <CardDescription>
              Create your first subject above to start organizing notes, documents, and insights.
            </CardDescription>
          </CardHeader>
        </Card>
      )}

      <Separator />
    </div>
  )
}
