"use client"

import axios from "axios"
import { useAuthStore } from "@/lib/store"

// Create a singleton Axios instance configured for the client app.
const api = axios.create({
  baseURL: process.env.NEXT_PUBLIC_API_BASE_URL,
})

// Attach Authorization header from the Zustand store for all requests
api.interceptors.request.use((config) => {
  const token = useAuthStore.getState().token
  if (token) {
    const headers = config.headers
    // Axios v1: headers may be AxiosHeaders or a plain object
    if (headers && typeof (headers as any).set === "function") {
      ;(headers as any).set("Authorization", `Bearer ${token}`)
    } else {
      config.headers = {
        ...(headers || {}),
        Authorization: `Bearer ${token}`,
      } as any
    }
  }
  return config
})

// On 401 responses, automatically clear the user's session
api.interceptors.response.use(
  (response) => response,
  (error) => {
    const status = error?.response?.status
    if (status === 401) {
      try {
        useAuthStore.getState().actions.logout()
      } catch (e) {
        // no-op: avoid breaking the promise chain; let the original error propagate
      }
    }
    return Promise.reject(error)
  }
)

export default api
